import tryAndCatchRequestWithErrorHandling from "../../components/errorCode/errorCode";
import API from "../API";

export function setUserList(data) {
    return { type: "setUserList", data };
}

export function getUserList(history) {
    return async (dispatch) => {
        async function successFunction(response) {
            dispatch(setUserList(response.user));
        }

        async function requestFunction() {
            return API.get(`user/getUsers`);
        }

        async function catchFunction() {
            history("/");
        }

        async function errorFunction() {
            history("/");
        }

        await tryAndCatchRequestWithErrorHandling(
            dispatch,
            null,
            requestFunction,
            1006,
            successFunction,
            errorFunction,
            catchFunction,
        );
    };
}