/* eslint-disable no-case-declarations */

/* eslint-disable no-restricted-syntax */
const data = {
    users: [],
};

// eslint-disable-next-line default-param-last
export default function adminReducer(state = data, action) {
    const temp = { ...state };
    switch (action.type) {
        case "setUserList":
            temp.users = action.data;
            return temp;
        default:
            return temp;
    }
}
