import React, {useEffect, useState} from 'react';
import { styled, Grid, Table, TableBody, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { getUserList } from "../../redux/actions/admin.action";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

const Users = () => {
  const dispatch = useDispatch();
  const history = useNavigate();
  const userList = useSelector((state) => state.admin.users);
  const [rows, setRows] = useState([]);

  useEffect(() => {
    dispatch(getUserList(history));
  }, [dispatch, history]);

  useEffect(() => {
    if(userList.length > 0){
      setRows(userList.map((item, i) => {
        return { 
          username: item.username, 
          email: item.email, 
          tel: item.tel, 
          level: item.level, 
          balanc: item.balanc, 
          invitedCount: item.invitedCount, 
          createdAt: item.createdAt }
      }))
    }
  }, [userList]);

  return (
    <Grid
        container
        justifyContent="center"
    >
      <Grid item xs={8} style={{ textAlign: "center", marginTop: "50px", border: "1px solid grey", padding: "50px", borderRadius: "10px"}}>
        <h1>Gift Users List</h1>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 700 }} aria-label="customized table">
            <TableHead>
              <TableRow>
                <StyledTableCell align="center">Username</StyledTableCell>
                <StyledTableCell align="center">Email</StyledTableCell>
                <StyledTableCell align="center">Tel</StyledTableCell>
                <StyledTableCell align="center">Level</StyledTableCell>
                <StyledTableCell align="center">Balanc</StyledTableCell>
                <StyledTableCell align="center">InvitedCount</StyledTableCell>
                <StyledTableCell align="center">CreatedAt</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <StyledTableRow key={row.username}>
                  <StyledTableCell align="center">{row.username}</StyledTableCell>
                  <StyledTableCell align="center">{row.email}</StyledTableCell>
                  <StyledTableCell align="center">{row.tel}</StyledTableCell>
                  <StyledTableCell align="center">{row.level}</StyledTableCell>
                  <StyledTableCell align="center">{row.balanc}</StyledTableCell>
                  <StyledTableCell align="center">{row.invitedCount}</StyledTableCell>
                  <StyledTableCell align="center">{row.createdAt}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
    
  );
}
export default Users;